.rose-container {
  position: absolute;
  top: -50px;
  left: 0;
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
}

.rose {
  animation: rose-fall 5s linear infinite;
}

@keyframes rose-fall {
  0% {
    transform: translateY(-50px);
    opacity: 1;
  }
  50% {
    transform: translateY(10vh);
  }
  100% {
    transform: translateY(100vh);
    opacity: 0;
  }
}
